	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import dianqiweixiu from '@/views/dianqiweixiu/list'
	import fangyuanxinxi from '@/views/fangyuanxinxi/list'
	import fangwuzulin from '@/views/fangwuzulin/list'
	import yuyuexinxi from '@/views/yuyuexinxi/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import weixiuxieyi from '@/views/weixiuxieyi/list'
	import discussfangyuanxinxi from '@/views/discussfangyuanxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import cuixiutongzhi from '@/views/cuixiutongzhi/list'
	import hetongxinxi from '@/views/hetongxinxi/list'
	import fangdong from '@/views/fangdong/list'
	import config from '@/views/config/list'
	import shenqingtuizu from '@/views/shenqingtuizu/list'
	import fangdongRegister from '@/views/fangdong/register'
	import fangdongCenter from '@/views/fangdong/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/fangdongCenter',
			name: '房东个人中心',
			component: fangdongCenter
		}
		,{
			path: '/news',
			name: '新闻资讯',
			component: news
		}
		,{
			path: '/dianqiweixiu',
			name: '电器维修',
			component: dianqiweixiu
		}
		,{
			path: '/fangyuanxinxi',
			name: '房源信息',
			component: fangyuanxinxi
		}
		,{
			path: '/fangwuzulin',
			name: '房屋租赁',
			component: fangwuzulin
		}
		,{
			path: '/yuyuexinxi',
			name: '预约信息',
			component: yuyuexinxi
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/weixiuxieyi',
			name: '维修协议',
			component: weixiuxieyi
		}
		,{
			path: '/discussfangyuanxinxi',
			name: '房源信息评论',
			component: discussfangyuanxinxi
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/cuixiutongzhi',
			name: '催修通知',
			component: cuixiutongzhi
		}
		,{
			path: '/hetongxinxi',
			name: '合同信息',
			component: hetongxinxi
		}
		,{
			path: '/fangdong',
			name: '房东',
			component: fangdong
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/shenqingtuizu',
			name: '申请退租',
			component: shenqingtuizu
		}
		]
	},
	{
		path: '/fangdongRegister',
		name: '房东注册',
		component: fangdongRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
