<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="24">
						<el-form-item label="房屋名称" prop="fangwumingcheng">
							<el-input class="list_inp" v-model="form.fangwumingcheng" placeholder="房屋名称"
								 type="text" 								:readonly="!isAdd||disabledForm.fangwumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="房屋图片" prop="fangwutupian">
							<uploads
								:disabled="!isAdd||disabledForm.fangwutupian?true:false"
								action="file/upload" 
								tip="请上传房屋图片" 
								:limit="3" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.fangwutupian?form.fangwutupian:''" 
								@change="fangwutupianUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="房屋面积" prop="fangwumianji">
							<el-input class="list_inp" v-model="form.fangwumianji" placeholder="房屋面积"
								 type="text" 								:readonly="!isAdd||disabledForm.fangwumianji?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="房屋地址" prop="fangwudizhi">
							<el-input class="list_inp" v-model="form.fangwudizhi" placeholder="房屋地址"
								 type="text" 								:readonly="!isAdd||disabledForm.fangwudizhi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="租赁价格" prop="zulinjiage">
							<el-input class="list_inp" v-model.number="form.zulinjiage" placeholder="租赁价格"
								 type="number" 								:readonly="!isAdd||disabledForm.zulinjiage?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="租赁押金" prop="zulinyajin">
							<el-input class="list_inp" v-model.number="form.zulinyajin" placeholder="租赁押金"
								 type="number" 								:readonly="!isAdd||disabledForm.zulinyajin?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="房屋视频" prop="fangwushipin">
							<uploads
								:disabled="!isAdd||disabledForm.fangwushipin?true:false"
								type="file"
								action="file/upload" 
								tip="请上传房屋视频" 
								:limit="1" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.fangwushipin?form.fangwushipin:''" 
								@change="fangwushipinUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="房东账号" prop="fangdongzhanghao">
							<el-input class="list_inp" v-model="form.fangdongzhanghao" placeholder="房东账号"
								 type="text" 								:readonly="!isAdd||disabledForm.fangdongzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="房东姓名" prop="fangdongxingming">
							<el-input class="list_inp" v-model="form.fangdongxingming" placeholder="房东姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.fangdongxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="联系电话" prop="lianxidianhua">
							<el-input class="list_inp" v-model="form.lianxidianhua" placeholder="联系电话"
								 type="text" 								:readonly="!isAdd||disabledForm.lianxidianhua?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="电子邮件" prop="dianziyoujian">
							<el-input class="list_inp" v-model="form.dianziyoujian" placeholder="电子邮件"
								 type="text" 								:readonly="!isAdd||disabledForm.dianziyoujian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="房屋状态" prop="fangwuzhuangtai">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.fangwuzhuangtai?true:false"
								v-model="form.fangwuzhuangtai" 
								placeholder="请选择房屋状态"
								>
								<el-option v-for="(item,index) in fangwuzhuangtaiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="地址" prop="fulladdress">
							<el-input class="list_inp" v-model="form.fulladdress" placeholder="地址" readonly @click.native="mapClick" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="房屋介绍" prop="fangwujieshao">
							<editor :value="form.fangwujieshao" placeholder="请输入房屋介绍" :readonly="!isAdd||disabledForm.fangwujieshao?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'fangwujieshao')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick">取消</el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
		<locationForm ref="location" :toParentsMap="form" @mapData="getAddress"></locationForm>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'fangyuanxinxi'
	const formName = '房源信息'
	//基础信息
	import locationForm from '@/components/common/location.vue'
	
	const location = ref(null)
	
	const mapClick = () => {
		if(!isAdd.value||disabledForm.fulladdress){
			return false
		}
		location.value.mapShow()
	}
	
	const getAddress = (e) => {
		form.value.lng = e.lng
		form.value.lat = e.lat
		form.value.fulladdress = e.fulladdress
	}
	//form表单
	const form = ref({})
	const disabledForm = ref({
		fangwumingcheng : false,
		fangwutupian : false,
		fangwumianji : false,
		fangwudizhi : false,
		zulinjiage : false,
		zulinyajin : false,
		fangwushipin : false,
		fangwujieshao : false,
		fangdongzhanghao : false,
		fangdongxingming : false,
		lianxidianhua : false,
		dianziyoujian : false,
		fangwuzhuangtai : false,
		storeupnum : false,
		clicktime : false,
		longitude : false,
		latitude : false,
		fulladdress : false,
		sfsh : false,
		shhf : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		fangwumingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		fangwutupian: [
		],
		fangwumianji: [
		],
		fangwudizhi: [
		],
		zulinjiage: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		zulinyajin: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		fangwushipin: [
		],
		fangwujieshao: [
		],
		fangdongzhanghao: [
		],
		fangdongxingming: [
		],
		lianxidianhua: [
			{ validator: validateMobile, trigger: 'blur' },
		],
		dianziyoujian: [
			{ validator: validateEmail, trigger: 'blur' },
		],
		fangwuzhuangtai: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		clicktime: [
		],
		longitude: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		latitude: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		fulladdress: [
		],
		sfsh: [
		],
		shhf: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//房屋图片上传回调
	const fangwutupianUploadSuccess=(e)=>{
		form.value.fangwutupian = e
	}
	//房屋视频上传回调
	const fangwushipinUploadSuccess=(e)=>{
		form.value.fangwushipin = e
	}
	//房屋状态列表
	const fangwuzhuangtaiLists = ref([])
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			fangwumingcheng: '',
			fangwutupian: '',
			fangwumianji: '',
			fangwudizhi: '',
			zulinjiage: '',
			zulinyajin: '',
			fangwushipin: '',
			fangwujieshao: '',
			fangdongzhanghao: '',
			fangdongxingming: '',
			lianxidianhua: '',
			dianziyoujian: '',
			fangwuzhuangtai: '未租赁',
			storeupnum: '0',
			clicktime: '',
			longitude: '',
			latitude: '',
			fulladdress: '',
			sfsh: '待审核',
			shhf: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.fangwujieshao = res.data.data.fangwujieshao?(res.data.data.fangwujieshao.replace(reg,'../../../cl07690162/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='fangwumingcheng'){
					form.value.fangwumingcheng = row[x];
					disabledForm.value.fangwumingcheng = true;
					continue;
				}
				if(x=='fangwutupian'){
					form.value.fangwutupian = row[x];
					disabledForm.value.fangwutupian = true;
					continue;
				}
				if(x=='fangwumianji'){
					form.value.fangwumianji = row[x];
					disabledForm.value.fangwumianji = true;
					continue;
				}
				if(x=='fangwudizhi'){
					form.value.fangwudizhi = row[x];
					disabledForm.value.fangwudizhi = true;
					continue;
				}
				if(x=='zulinjiage'){
					form.value.zulinjiage = row[x];
					disabledForm.value.zulinjiage = true;
					continue;
				}
				if(x=='zulinyajin'){
					form.value.zulinyajin = row[x];
					disabledForm.value.zulinyajin = true;
					continue;
				}
				if(x=='fangwushipin'){
					form.value.fangwushipin = row[x];
					disabledForm.value.fangwushipin = true;
					continue;
				}
				if(x=='fangwujieshao'){
					form.value.fangwujieshao = row[x];
					disabledForm.value.fangwujieshao = true;
					continue;
				}
				if(x=='fangdongzhanghao'){
					form.value.fangdongzhanghao = row[x];
					disabledForm.value.fangdongzhanghao = true;
					continue;
				}
				if(x=='fangdongxingming'){
					form.value.fangdongxingming = row[x];
					disabledForm.value.fangdongxingming = true;
					continue;
				}
				if(x=='lianxidianhua'){
					form.value.lianxidianhua = row[x];
					disabledForm.value.lianxidianhua = true;
					continue;
				}
				if(x=='dianziyoujian'){
					form.value.dianziyoujian = row[x];
					disabledForm.value.dianziyoujian = true;
					continue;
				}
				if(x=='fangwuzhuangtai'){
					form.value.fangwuzhuangtai = row[x];
					disabledForm.value.fangwuzhuangtai = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='clicktime'){
					form.value.clicktime = row[x];
					disabledForm.value.clicktime = true;
					continue;
				}
				if(x=='longitude'){
					form.value.longitude = row[x];
					disabledForm.value.longitude = true;
					continue;
				}
				if(x=='latitude'){
					form.value.latitude = row[x];
					disabledForm.value.latitude = true;
					continue;
				}
				if(x=='fulladdress'){
					form.value.fulladdress = row[x];
					disabledForm.value.fulladdress = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.fangwuzhuangtai='未租赁'
			form.value.storeupnum='0'
			form.value.sfsh='待审核'
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('fangdongzhanghao')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.fangdongzhanghao = json.fangdongzhanghao
				disabledForm.value.fangdongzhanghao = true;
			}
			if(json.hasOwnProperty('fangdongxingming')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.fangdongxingming = json.fangdongxingming
				disabledForm.value.fangdongxingming = true;
			}
			if(json.hasOwnProperty('lianxidianhua')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.lianxidianhua = json.lianxidianhua
				disabledForm.value.lianxidianhua = true;
			}
			if(json.hasOwnProperty('dianziyoujian')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.dianziyoujian = json.dianziyoujian
				disabledForm.value.dianziyoujian = true;
			}
		})
		fangwuzhuangtaiLists.value = "已租赁,未租赁".split(',')
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		if(form.value.fangwutupian!=null) {
			form.value.fangwutupian = form.value.fangwutupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(form.value.fangwushipin!=null) {
			form.value.fangwushipin = form.value.fangwushipin.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		border: 1px solid rgba(254, 182, 203, 0.5);
		border-radius: 0px;
		padding: 40px 30px;
		margin: 0;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			margin: 0 150px 20px 0;
			background: none;
